import { cs } from '@mx-design/web-utils';
import { useMemo } from 'react';
export function useClassNames(props) {
  const {
    getPrefixCls,
    overlayClassName
  } = props;
  const prefixCls = getPrefixCls('tooltip');
  return useMemo(() => ({
    toolTipCls: cs(`${prefixCls}`, overlayClassName)
  }), [overlayClassName, prefixCls]);
}