import { useMemo } from 'react';
import { cs } from '@mx-design/web-utils';

// type

export function useClassNames(props) {
  const {
    context,
    disabled,
    checked,
    className,
    getPrefixCls
  } = props;
  const prefixCls = getPrefixCls('radio');
  return useMemo(() => ({
    wrapperCls: cs(`${prefixCls}${context.type === 'button' ? '-button' : ''}`, {
      [`${prefixCls}-checked`]: checked,
      [`${prefixCls}-disabled`]: disabled
    }, className),
    maskCls: `${prefixCls}-mask`,
    textCls: `${prefixCls}-text`,
    buttonInnerCls: `${prefixCls}-button-inner`
  }), [disabled, checked, className, context]);
}