import { cs } from '@mx-design/web-utils';
import { useMemo } from 'react';
export function useLinkClassNames(props) {
  const {
    getPrefixCls,
    currentLink,
    href,
    className
  } = props;
  const prefixCls = getPrefixCls('anchor-link');
  return useMemo(() => ({
    linkCls: cs(prefixCls, {
      [`${prefixCls}-active`]: currentLink === href
    }, className),
    titleCls: `${prefixCls}-title`
  }), [getPrefixCls, currentLink, href, className]);
}